<template>
	<view class="search-page">
		<!-- 搜索框 -->
		<view class="top-box">
			<span v-if="!isIndex" class="top-exit" @click="toSearchIndex"> {{exitTitle}}</span>
			<view class="top-search" >
				<text @click="search"></text>
				<input placeholder="iPhone 14" v-model="searchValue" confirm-type="search" @confirm="search">
			</view>
		</view>
		
		
		
		<view class="search-wrap" v-if="isIndex">
			<view class="history-wrap" v-if="historyWords.length != 0">
				<view class="search-header">
					<text class="search-title">历史搜索</text>
					<text class="search-clear" @click="clearHistory">清除</text>
				</view>
				
				<view class="search-content">
					<view
					  class="search-item"
					  hover-class="hover-history-item"
					  v-for="(item,index) of historyWords"
					  :key="index"
					  @click="historyTap(item)"
					  @longtap="clearOneHistory(index)"
					>
						{{item}}
					</view>
				</view>
			</view>
			
			<view class="popular-wrap" v-if="popularWords.length !== 0">
				<view class="search-header">
					<text class="search-title">热门搜索</text>
				</view>
				
				<view class="search-content">
					<view
					  class="search-item"
					  hover-class="hover-history-item"
					  v-for="(item,index) of popularWords"
					  :key="index"
					  @click="popularTap(item)"
					  
					>
						{{item.content}}
					</view>
				</view>
			</view>
		</view>
			
		<view v-else class="search-res-page">
			<!-- 找到结果 -->
			<view v-if="searchResult.length !== 0">
				<view class="res-title">共找到{{searchResult.length}}条结果</view>
				<view class="search-result">
					<!-- 每个搜索结果 -->
										
					<view class="result-item">
						<Goods class="goods"
							v-for="(item,index) of searchResult" 
							:key="index"
							:goods="item"
						 >
						 </Goods>
						 
					</view>
				</view>
				<Nomore notips="没有更多了...."></Nomore>
			</view>
			<view v-else class="no-result">
				<u-empty mode="search"></u-empty>
			</view>
		</view>
		
	</view>
	

</template>

<script>
	import Goods from '@/components/goods/index.vue'
	import Search from '@/components/search/index.vue'
	import Nomore from '@/components/nomore/index.vue'
	
	export default {
		components:{Goods,Search,Nomore},
		data() {
			return {
				
				historyWords: [],
				popularWords: [],
				searchResult:[],
				searchValue: '',
				isIndex : true,
				exitTitle:'返回',
				
				ispopular: false
				    
			}
		},
		mounted(){
			// uni.setStorageSync('historyWords',['鸡','电脑','iPhone12','车载手表','自然堂','小米10','华为','氢跑鞋','娃娃']);
			// uni.setStorageSync('popularWords',['iPhone12','车载手表','自然堂','小米10']);
			this.queryHistory();
			this.queryPopular();
		},
		
		methods:{
			queryHistory() {
				try{
					this.historyWords = uni.getStorageSync('historyWords') || [];
				}catch(e){
					//TODO handle the exception
					this.$toast(e);
					this.historyWords = [];
				}
			},
			
			async queryPopular() {
				try{
					this.popularWords = await this.api.get('/hotkeys')
				}catch(e){
					//TODO handle the exception
					this.popularWords = [];
					this.$toast(e);
				}
			},
		
			clearHistory(){
				const that = this;
				uni.showModal({    // 弹框询问是否进行下一步事件
				        title: '提示',
				        content: '是否删除该记录',
				        success: function(res) {
				            if (res.confirm) {
				                that.historyWords = [];
				                uni.removeStorageSync('historyWords');
				
				            } else if (res.cancel) {
				                console.log('用户点击取消');
				                return
				            }
				        }
				    });
			},
		
			historyTap(item){
				this.searchValue = item;
				this.ispopular = false;
				this.search(item);
			},
			popularTap(item){
				this.searchValue = item.content;
				this.ispopular = true;
				this.search(item.id);
			},
			
			async search(key){
				let keyword = this.searchValue;
				this.isIndex = false;
				if(keyword.trim() !== ''){
					this.historyWords = this.historyWords.filter(item=>item != keyword);
					this.historyWords.unshift(keyword);
					// 存储
					uni.setStorageSync('historyWords',this.historyWords);
				}
				
				// 发送请求
				try{
					let res = '';
					if(this.ispopular) res = await this.api.get('/hotkey/products',{id:key});
					else res = await this.api.get('/search/products',{keyword});
					this.searchResult = res;
					console.log(this.searchResult);
				}catch(e){
					//TODO handle the exception
					this.$toast(e)
				}
				
				
			},
			clearOneHistory(index){
				const that = this;
				
				uni.showModal({    // 弹框询问是否进行下一步事件
				        title: '提示',
				        content: '是否删除该记录',
				        success: function(res) {
				            if (res.confirm) {
				                that.historyWords.splice(index,1);
				                uni.setStorageSync('historyWords',that.historyWords);
				
				            } else if (res.cancel) {
				                console.log('用户点击取消');
				                return
				            }
				        }
				    });
				
			},
			toSearchIndex(){
				this.isIndex = true
			}
		}		
	}

</script>

<style >
	.top-box{
		display: flex;
		justify-content: space-around;
		align-items: center;
	}
	/* 顶部搜索 */
	
	.top-search {
		display: flex;
		align-items: center;
		margin: 20rpx auto 16rpx auto;
		width: 90%;
		height: 64rpx;
		background-color: #ebebeb;
		border-radius: 30rpx;
	}
	.top-search>text {
		width: 50rpx;
		height: 50rpx;
		margin: 0 20rpx;
		line-height: 64rpx;
		background-image: url('../../static/image/search.png');
		background-size: 100%;
		
	}
	.top-search >input {
		flex:0.88;
		height: 100%;
		font-size: 28rpx;
	}
	
	.top-exit {
		height: 50rpx;
		width: 60rpx;
		line-height: 50rpx;
		font-size: 24rpx;
		text-align: center;
		background-color: #dadada;
		border-radius: 5rpx;
	}
	/* ------------------- */
	
	
	.search-page {
	  box-sizing: border-box;
	  width: 100vw;
	  height: 100vh;
	  padding: 0 30rpx;
	}

	
	.search-page .search-wrap {
	  margin-top: 44rpx;
	}

	.search-page .history-wrap {
	  margin-bottom: 20px;
	}

	.search-page .search-header {
	  display: flex;
	  flex-flow: row nowrap;
	  justify-content: space-between;
	  align-items: center;
	}

	.search-page .search-title {
	  font-size: 30rpx;
	  font-family: PingFangSC-Semibold, PingFang SC;
	  font-weight: 600;
	  color: rgba(51, 51, 51, 1);
	  line-height: 42rpx;
	}

	.search-page .search-clear {
	  font-size: 24rpx;
	  font-family: PingFang SC;
	  line-height: 32rpx;
	  color: #999999;
	  font-weight: normal;
	}

	.search-page .search-content {
	  overflow: hidden;
	  display: flex;
	  flex-flow: row wrap;
	  justify-content: flex-start;
	  align-items: flex-start;
	  margin-top: 24rpx;
	}

	.search-page .search-item {
	  color: #333333;
	  font-size: 24rpx;
	  line-height: 32rpx;
	  font-weight: normal;
	  margin-right: 24rpx;
	  margin-bottom: 24rpx;
	  background: #f5f5f5;
	  border-radius: 38rpx;
	  padding: 12rpx 24rpx;
	}

	.search-page .hover-history-item {
	  position: relative;
	  top: 3rpx;
	  left: 3rpx;
	  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.1) inset;
	}

	.add-notes__confirm {
	  color: #fa4126 !important;
	}
	
	
	.search-res-page .res-title {
		margin: 20rpx 0;
		font-size: 36rpx;
		color: #fa4126;
		text-shadow: 4rpx 4rpx 6rpx rgba(0, 0, 0, 0.3);
	}
	
	.result-item {
		width: 100%;
		margin:20rpx 0;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	.goods{
		width: 47.5%;
		height: 340rpx;
		margin-top: 10rpx;
	}
	
	/* 没有结果 */
	.no-result {
		margin-top: 200rpx;
	}

</style>
